.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 317px;
    z-index: 3;
    position: absolute;
    background-color: #FFFFFF;
    box-shadow: 0px 10px 40px rgba(29, 33, 38, 0.13), 0px 1px 2px rgba(29, 33, 38, 0.1);
}

.background {
    width: 1700px;
    height: 100%;
    background-color: black;
    opacity: 50%;
    z-index: 1;
    position: absolute;
}

.nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 70px;
    margin-top: 30px;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;

}

.nav a {
    color: black;
    margin: 15px;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;
}

.close {
    background-color: #ffffff;
    border: none;
    align-self: flex-end;
    margin: 10px;
    width: 24px;
}

